.wrapper{height:270px}.wrapper>:not(:last-child){border-bottom:1px solid var(--borderLine)}.header>p{height:30px;white-space:nowrap;overflow:hidden;text-overflow:ellipsis}.header,.transactionsElement{display:flex;margin:0;padding:0}.header :first-child,.transactionsElement :first-child{width:20%}.header :nth-child(2),.transactionsElement :nth-child(2){width:15%;text-align:center}.header :nth-child(3),.transactionsElement :nth-child(3){width:15%}.header :nth-child(4),.transactionsElement :nth-child(4){width:15%}.header :nth-child(5),.transactionsElement :nth-child(5){width:20%}.header :nth-child(6),.transactionsElement :nth-child(6){width:15%}.header :last-child,.transactionsElement :last-child{width:15%}.header{height:35px;color:var(--text-color)}.transactionsElement{display:flex;align-items:center;margin-bottom:5px;height:65px}.transactionsElement>.trans_title{height:40px;display:flex;gap:10px;align-items:center}.transactionsElement>.trans_title>p{width:70%;white-space:nowrap;overflow:hidden;text-overflow:ellipsis}.transactionsElement>p{margin:0;white-space:nowrap;overflow:hidden;text-overflow:ellipsis}.transactionsElement>button{color:var(--title);background-color:rgba(0,0,0,0);border:1px solid var(--title);border-radius:150px;font-size:0.9375rem;height:45px;font-weight:400;letter-spacing:1;transition:background,color,border,var(--transition)}@media(any-hover: hover){.transactionsElement>button:hover{border:none;background-color:#539bff;color:var(--background)}}@media(any-hover: none){.transactionsElement>button:active{border:none;background-color:#539bff;color:var(--background)}}.green{color:var(--green)}.red{color:var(--red)}